@charset "utf-8";
@use "base/base" as *;
@use "parts/header";
@use "parts/footer";

@keyframes slideDown {
  0% {
    height: 100%;
    transform: translateY(0%);
  }

  50% {
    height: 95%;
  }

  100% {
    height: 25%;
    transform: translateY(100%);
  }
}

@keyframes slideUp {
  0% {
    height: 100%;
    transform: translateY(0%);
  }

  50% {
    height: 95%;
  }

  100% {
    height: 25%;
    transform: translateY(-100%);
  }
}

.keyvisual-top-primary {
  .keyvisual-viewport {
    position: relative;
  }
}

.slider-top-content {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  text-align: center;
  color: #fff;
  box-sizing: border-box;
  overflow: hidden;
  visibility: hidden;
  pointer-events: none;

  * {
    box-sizing: border-box;
  }

  .slider-viewport,
  .slider-wrapper {
    width: 100%;
    height: 100%;
  }

  .slider-slide {
    padding: 0 50px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: none !important;
    visibility: visible;

    .slide-motto,
    .slide-title,
    .slide-detail {
      display: block;
      transform: translateY(100%);
      opacity: 0;
    }

    .slide-motto,
    .slide-title {
      font-weight: 700;
    }

    .slide-motto {
      margin-bottom: 10px;
      font-size: 20px;
      line-height: calc(32 / 20);
    }

    .slide-title {
      margin-bottom: 20px;
      font-size: 48px;
      line-height: calc(56 / 48);
    }

    .slide-detail {
      font-size: 16px;
      line-height: calc(24 / 16);
    }
  }

  .slider-slide.swiper-slide-active {
    .slide-motto,
    .slide-title,
    .slide-detail {
      transform: translateY(0);
      opacity: 1;
    }

    .slide-motto {
      transition: transform 0.5s ease, opacity 0.5s ease;
    }

    .slide-title {
      transition: transform 0.5s 0.25s ease, opacity 0.5s 0.25s ease;
    }

    .slide-detail {
      transition: transform 0.5s 0.75s ease, opacity 0.5s 0.75s ease;
    }
  }
}

.slider-top-background {
  position: relative;
  box-sizing: border-box;
  overflow: hidden;

  &::before {
    content: "";
    display: block;
    position: relative;
    width: 100%;
    padding-top: calc(1080 / 1920 * 100%);
    pointer-events: none;
  }

  * {
    box-sizing: border-box;
  }

  .slider-viewport {
    margin: auto;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .slider-arrow {
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-color: rgba(0, 0, 0, .5);
    position: absolute;
    top: calc(50% - 30px);
    z-index: 1;
    cursor: pointer;
    transition: background 0.5s ease;

    &::after {
      content: "";
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 0 5px 10px;
      border-color: transparent transparent transparent #fff;
      position: absolute;
      top: calc(50% - 5px);
      left: calc(50% - 5px);
    }

    &:hover {
      background-color: rgba(255, 255, 255, .5);
    }
  }

  .slider-arrow.swiper-button-prev {
    left: 30px;
    transform: scale(-1, 1);
  }

  .slider-arrow.swiper-button-next {
    right: 30px;
  }

  .slider-slide {
    position: relative;
    z-index: 0;
    overflow: visible;

    .slide-wrapper {
      position: relative;
      padding-top: calc(1080 / 1920 * 100%);
    }

    .slide-upper,
    .slide-below {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;

      &::before {
        content: "";
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        pointer-events: none;
      }

      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }

    .slide-upper {
      z-index: 1;
      opacity: 0;
      transform: translateY(0);

      &::before {
        background-color: rgba(0, 0, 0, 0.6);
      }
    }

    .slide-below {
      opacity: .5;
      z-index: 0;
      transition: opacity 2s linear;

      &::before {
        background-color: rgba(0, 0, 0, 0.2);
      }
    }
  }

  .slider-slide.is-animation-previous,
  .slider-slide.is-animation-next {
    z-index: 1;

    .slide-upper,
    .slide-below {
      opacity: 1;
    }
  }

  .slider-slide.is-animation-previous {
    .slide-upper {
      top: auto;
      animation: slideDown 2s linear forwards;
    }
  }

  .slider-slide.is-animation-next {
    .slide-upper {
      bottom: auto;
      animation: slideUp 2s linear forwards;
    }
  }
}
